.video-popup {
  margin-top: calc($margin-sm * 3);

  @include respond(tablet) {
    margin-top: $margin-md;
  }
  cursor: pointer;
  .play-circle {
    display: inline-block;
    vertical-align: middle;
    img {
      @include respond(phone) {
        width: 28px;
        height: 28px;
      }
    }
  }
  h2 {
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase;
    margin-left: $margin-sm;
    vertical-align: middle;

    @include respond(tablet) {
      font-size: 18px;
    }
    @include respond(tablet) {
      font-size: 14px;
    }
  }
}
