* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
}
ul {
  list-style: none;
}

img {
  color: $color-secondary;
  font-size: 14px;
  font-weight: 700;
}
.container-lg {
  max-width: 1820px;
  margin: 0 auto;
}
.container-md {
  max-width: 1280px;
  margin: 0 auto;
}
.container-sm {
  max-width: 1140px;
  margin: 0 auto;
  @include respond(laptop) {
    padding: 0 $padding-lg;
  }
  @include respond(phone) {
    padding: 0 $padding-md;
  }
  @include respond(phone-xs) {
    padding: 0 $padding-sm;
  }
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.inline-block {
  display: inline-block;
}

.on-scroll {
  background: $color-primary;
}

.content-link {
  color: $color-secondary;
}
