footer {
  background: #f9fcff;
  padding: calc(2 * $padding-x-lg) 0 $padding-x-lg;

  @include respond(laptop) {
    padding: $padding-x-lg 0 $padding-lg;
  }

  .footer-top {
    .promotional-msg {
      width: 46%;
      max-width: 480px;
      line-height: 38px;
      letter-spacing: 0;
      margin: 0 auto;
      span.u__txt-green {
        font-weight: 700;
      }

      @include respond(laptop) {
        width: 60%;
      }
      @include respond(phone-sm) {
        width: 100%;
      }
    }
  }
  .input-grp {
    margin-top: calc(2 * $margin-lg);
    border: 1px solid #9aa8b6;
    border-radius: 5px;
    padding: calc($padding-sm/2);
    background: #fff;
    max-width: 710px;
    display: inline-block;
    width: 65%;

    @include respond(tablet) {
      margin-top: $margin-lg;
    }
    @include respond(phone) {
      width: 80%;
    }
    @include respond(phone-sm) {
      width: 100%;
    }
    input {
      font-size: 14px;
      line-height: 51px;
      letter-spacing: 0;
      color: #61788e;
      border: none;
      width: 70%;

      @include respond(tablet) {
        line-height: 45px;
      }

      &::placeholder {
        text-align: center;
        color: #61788e;
      }
      &:focus {
        outline: 1px solid $color-secondary;
        border-radius: 5px;
      }
    }
    .btn--lg {
      width: 30%;
    }
  }
  .footer-bottom {
    .container-lg {
      padding: $padding-x-lg (2 * $padding-lg) 0;

      @include respond(tablet) {
        padding: $padding-lg $padding-lg 0;
      }

      .disclaimer {
        font-size: 14px;
        font-weight: 700;
        letter-spacing: 0;
        color: #9aa8b6;
        span {
          color: #61788e;
        }

        @include respond(tablet) {
          font-size: 12px;
        }
        @include respond(phone) {
          width: 100%;
          text-align: center;
          margin-bottom: $margin-md;
        }
      }
      .socialize {
        @include respond(phone) {
          width: 100%;
          text-align: center;
        }
        a {
          line-height: 20px;
          img {
            vertical-align: middle;
          }
          &:not(:last-child) {
            margin-right: $margin-lg;

            @include respond(tablet) {
              margin-right: $margin-md;
            }
          }
        }
      }
    }
  }
}
