// montserrat font
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap");

body {
  font-family: "Montserrat", sans-serif;
}

.heading-x-lg {
  font-size: 80px;
  line-height: 92px;
  letter-spacing: 0;
  font-weight: 700;
  color: $color-tertiary;
  @include respond(tablet) {
    font-size: 60px;
    line-height: 80px;
  }

  @include respond(phone) {
    font-size: 45px;
  }
  @include respond(phone-sm) {
    font-size: 40px;
    line-height: 50px;
  }
  @include respond(phone-xs) {
    font-size: 30px;
    line-height: 40px;
  }
}
.heading-lg {
  text-transform: uppercase;
  font-size: 50px;
  font-weight: 700;
  letter-spacing: 0;
  color: $color-primary;

  @include respond(laptop) {
    font-size: 40px;
  }
  @include respond(phone) {
    font-size: 30px;
  }
}

.text-md {
  font-size: 22px;
  font-weight: 500;
  line-height: 34px;
  letter-spacing: 0;
  color: #9aa8b6;
  @include respond(laptop) {
    font-size: 20px;
  }
  @include respond(tablet) {
    font-size: 18px;
  }
  @include respond(phone) {
    font-size: 14px;
  }
}
.text-sm {
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 0;
  font-weight: 700;
  color: $color-primary;

  @include respond(phone) {
    font-size: 12px;
  }
}
