.carousel {
  padding: 100px 0;
  position: relative;
  .dot-nav {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 3px;
    .dot {
      display: inline-block;
      height: 12px;
      width: 12px;
      border-radius: 50%;
      background: #dfe4e8;
      &:not(:last-child) {
        margin-right: 28px;
      }
    }
  }
  //   .carousel__track-wrapper {
  //     .carousel__track {
  //     }
  //   }
}

.carousel__slide {
  .carousel__img {
    margin-right: 100px;

    @include respond(laptop) {
      height: 500px;
      width: 500px;
      margin-right: 60px;
    }
    @include respond(tablet) {
      float: none;
      margin: 0;
      margin-bottom: $margin-lg;
    }
    @include respond(phone) {
      height: 400px;
      width: 400px;
    }
    @include respond(phone-sm) {
      height: 300px;
      width: 300px;
    }
    @include respond(phone-xs) {
      height: 250px;
      width: 250px;
    }
  }
  h3,
  p {
    margin-top: calc($margin-lg + 15px);

    @include respond(laptop) {
      margin-top: calc($margin-md + 15px);
    }
    @include respond(phone) {
      margin-top: calc($margin-sm + 15px);
    }
  }
}
