.section-hero {
  height: 100vh;
  max-height: 1080px;
  background: linear-gradient(to top, rgba(0, 139, 190, 0.7), rgba(13, 145, 193, 0.356), rgba(24, 151, 196, 0), rgba(255, 255, 255, 0)), url("../img/turtle@2x.jpg");
