.header {
  position: fixed;

  top: 0;
  left: 0;
  width: 100%;
  padding: $padding-lg;
  text-align: center;
  z-index: 20;
  transition: all 0.2s ease-in-out;
  &.on-scroll {
    padding: $padding-md $padding-lg;
  }

  @include respond(phone-sm) {
    padding: $padding-md;
  }
  @include respond(phone-xs) {
    padding: $padding-md $padding-sm;
  }

  .logo {
    @include respond(tablet) {
      float: none;
      display: inline-block;
    }
    img {
      @include respond(phone-xs) {
        width: 150px;
        // height: 80%;
      }
    }
  }
  a {
    line-height: 35px;
    color: #fff;
    img {
      vertical-align: middle;
    }
  }
  .explore {
    a {
      &:not(:last-child) {
        margin-right: 3 * $margin-sm;

        @include respond(phone-sm) {
          margin-right: 2 * $margin-sm;
        }
        @include respond(phone-xs) {
          margin-right: $margin-sm;
        }
      }
    }
  }
}
