.column {
  width: 41.23%;
  @include respond(tablet) {
    width: 100%;
    &:not(:last-child) {
      margin-bottom: $margin-lg;
    }
  }
  img {
    width: 100%;
    max-width: 470px;
    display: inline-block;
  }
  .content-title {
    padding-top: $padding-md;
  }
  .content-subtitle {
    margin-top: calc($margin-lg + 15px);

    @include respond(tablet) {
      margin-top: calc($margin-md + 15px);
    }
  }
  .content-description {
    margin-top: $margin-lg;
    @include respond(tablet) {
      margin-top: $margin-md;
    }
  }
  .content-link {
    margin-top: $margin-lg;
    @include respond(tablet) {
      margin-top: $margin-md;
    }
  }
}

.stats-row {
  .stats-col:first-child {
    padding-top: $padding-lg;
    width: 65%;
    p {
      width: 65%;
    }

    @include respond(laptop) {
      width: 60%;
    }
    @include respond(tablet) {
      width: 100%;
      margin-bottom: $margin-lg;
      p {
        width: 100%;
      }
    }
  }
  .stats-col:last-child {
    width: 35%;
    text-align: right;
    @include respond(laptop) {
      width: 40%;
    }

    @include respond(tablet) {
      width: 100%;
      text-align: center;
    }
  }
}
