.counter-col {
  display: inline-block;
  width: 220px;
  text-align: left;
  &:not(:last-child) {
    margin-right: 35px;
  }

  @include respond(laptop) {
    width: 150px;
    text-align: center;
  }
  @include respond(phone) {
    width: 100%;

    &:not(:last-child) {
      margin: 0;
      margin-bottom: $margin-lg;
    }
  }
  &--center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .digit {
    display: inline-block;
    font-size: 80px;
    font-weight: 300;
    letter-spacing: 0;
    color: $color-secondary;
    margin-bottom: $margin-sm;
    @include respond(tablet) {
      font-size: 60px;
    }

    @include respond(phone) {
      font-size: 45px;
    }
    @include respond(phone-sm) {
      font-size: 40px;
    }
    @include respond(phone-xs) {
      font-size: 30px;
    }
  }
}
