.section-hero {
  height: 100vh;
  max-height: 1080px;
  background: linear-gradient(
      to top,
      rgba(0, 139, 190, 0.7),
      rgba(13, 145, 193, 0.356),
      rgba(24, 151, 196, 0),
      rgba(255, 255, 255, 0)
    ),
    url("../img/turtle@2x.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  .container-md {
    position: relative;
    height: 100%;

    .hero-content {
      position: absolute;
      top: 50%;
      color: #fff;
      transform: translateY(-50%);
      @include respond(laptop) {
        padding: 0 $padding-lg;
      }
      @include respond(phone) {
        padding: 0 $padding-md;
      }
      @include respond(phone-sm) {
        padding: 0 $padding-sm;
      }

      .hero-text {
        display: inline-block;
        color: #fff;
        padding-bottom: calc($padding-sm / 2);
        position: relative;
        text-transform: capitalize;
        margin-bottom: $margin-lg;
        @include respond(tablet) {
          margin-bottom: $margin-md;
        }
        &::after {
          content: "";
          width: 100%;
          position: absolute;
          left: 0;
          height: 3px;
          background-color: $color-tertiary;
          bottom: 0;
        }
      }
    }
  }
}

[class^="section-"]:not([class$="-hero"]) {
  padding-top: calc($padding-lg * 4);
  @include respond(tablet) {
    text-align: center;
    padding-top: calc($padding-lg * 3);
  }
  @include respond(phone) {
    padding-top: calc($padding-lg * 2);
  }
}

.section-stats {
  .counter-row {
    margin-top: calc($margin-sm * 3);
  }
  .counter-square {
    border: 4px solid $color-secondary;
    border-radius: 4px;
    height: 370px;
    width: 370px;
    text-align: center;
    position: relative;

    @include respond(phone) {
      height: 320px;
      width: 320px;
    }
    @include respond(phone-sm) {
      height: 280px;
      width: 280px;
    }
    @include respond(phone-xs) {
      height: 200px;
      width: 200px;
    }
  }
}

.section-news {
  padding-bottom: calc(2 * $padding-x-lg);
  .row {
    padding: $padding-x-lg 0 calc(2 * $padding-x-lg);

    @include respond(tablet) {
      padding: $padding-lg 0 calc(2 * $padding-lg);
    }
    .column {
      h3 {
        margin-top: calc(2 * $margin-md);
        text-transform: capitalize;
        & + p {
          font-size: 14px;
          letter-spacing: 0;
          color: #9aa8b6;
          font-weight: 500;
          margin-top: calc(3 * $margin-sm);

          @include respond(phone) {
            margin-top: calc(2 * $margin-sm);
          }
        }
      }

      img {
        margin-top: calc(3 * $margin-sm);
        & + p {
          margin-top: calc(3 * $margin-md);
          @include respond(tablet) {
            margin-top: calc(2 * $margin-md);
          }
          @include respond(phone) {
            margin-top: $margin-md;
          }
        }
      }
    }
  }
}
